%btn {
  color: #fff;
  padding: 0.75rem 1.2rem;
  display: inline-block;
  text-align: center;
}

.btn-primary {
  @extend %btn;
  background-color: $dark-blue;
}

.btn-success {
  @extend %btn;
  background-color: $green;
}

.btn-alert {
  @extend %btn;
  background-color: tomato;
}
